
<template>
    <div id="webtemplate">
        <div v-if="!edit" class="center">
            <strong style="margin-bottom: 12px">¿Crees que la encuesta funciona bien?</strong>
            <div class="card" v-for="(answer, i) in content.poll" @click="vote(i)" :key="i" :style="`background: linear-gradient(90deg, rgba(79,156,209,1) 0%, rgba(79,156,209,1) ${Math.floor((answer.value/total)*100)}%, rgba(255,255,255,1) ${Math.floor((answer.value/total)*100) || 0}%, rgba(255,255,255,1) 100%);`">
                <div v-if="selected != -1" style="font-weight: bold; font-stretch: ultra-condensed; border-right: 1px solid #E0E0E0; padding-right: 8px;">{{Math.floor((answer.value/total)*100)}}%</div>
                <div v-else style="font-weight: bold; font-stretch: ultra-condensed; border-right: 1px solid #E0E0E0; padding-right: 8px;">{{i}}</div>
                <div style="padding-left: 8px">{{answer.text}}</div>
            </div>
        </div>
        <div v-else class="center">
            <button class="resetButton" @click="restartAnswers">Reset</button>
            <div class="card" v-for="(answer, i) in content.poll" :key="i">
                <div style="font-weight: bold; font-stretch: ultra-condensed; border-right: 1px solid #E0E0E0; padding-right: 8px;">{{i}}</div>
                <div style="padding-left: 8px">{{answer.text}}</div>
            </div>
            <div class="card" style="padding: 0">
                <input v-model="text" @keyup.enter="addAnswer" type="text" placeholder="Respuesta" class="input">
                <button @click="addAnswer" class="button">+</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'web-template',

    data() {
        return {
            text: "",
            total: 0,
            selected: -1
        }
    },

    props: {
        content: Object, 
        user: Object, 
        card: Object,
        isMiniature: Boolean,
        edit: Boolean
    },

    methods: {
        addAnswer() {
            if (!this.content.poll) {
                this.content.poll = []
            }
            this.content.poll.push({
                text: this.text,
                value: 0
            })

            this.text = ""
        },

        restartAnswers() {
            this.content.poll = []
        },

        vote(index) {
            if (this.selected != -1) return 
            this.content.poll[index].value++
            this.total += 1
            this.selected = index
            this.$emit("save")
        }
    },

    mounted() {
        if (!this.content.poll) {
            this.total = 0
        }
        else {
            for(var i = 0; i < this.content.poll.length; i++)
            {
                this.total += this.content.poll[i].value;
            }
        }
    }
}
</script>

